import React, {Component} from 'react';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';

import JiraService from '../../services/jira.service';
import BadgeComponent from "./badge.component";
import './styles.css'
import PanelLoaderComponent from "../../components/panel-loader";


function linkFormatter(issue, row) {
    return (
        <a target={'_blank'} href={row.self}>{issue}</a>
    );
}

function badgesFormatter(badges, row) {
    return (
        <div className={"btn-group btn-progress"}>
            {badges.filter(b => b.type !== 'all').map((b, i) =>
                <BadgeComponent key={i} badge={b}/>
            )}
        </div>
    );
}


class JiraBoardIssues extends Component {
    constructor() {
        super();
        this.state = {
            data: {
                items: []
            }
        };
    }

    getBoardIssues() {
        var self = this;
        new JiraService().getBoardIssues()
            .then(response => {
                if (response.data) {
                    self.state.data.items = response.data;
                    self.setState({
                        data: self.state.data
                    });
                }
            });
    }

    componentDidMount() {
        var self = this;
        self.getBoardIssues();
        const refreshIntervalId = setInterval(() => {
            self.getBoardIssues(refreshIntervalId);
        }, 1000*60*5);

        this.setState({
            refreshIntervalId,
        });
    }

    componentWillUnmount() {
        clearInterval(this.state.refreshIntervalId);
    }

    render() {
        return (this.state.data.items.length
                ? <div>
                    <div className={"jira-header"}>
                        <div className={"btn-group "}>
                            <button type="button" className="btn btn-warning">Development</button>
                            <button type="button" className="btn btn-primary">In Progress</button>
                            <button type="button" className="btn btn-info">Quality Assurence</button>
                            <button type="button" className="btn btn-success">Done</button>
                        </div>
                    </div>
                    <p></p>
                    <div className={'jira-content'}>
                        <div className={'jira-table'}>
                            <BootstrapTable data={this.state.data.items} striped hover version='4'>
                                <TableHeaderColumn dataField='key' isKey width='15%'
                                                   dataFormat={linkFormatter}>Key</TableHeaderColumn>
                                <TableHeaderColumn dataField='summary' width='55%'
                                                   dataFormat={linkFormatter}>Summary</TableHeaderColumn>
                                <TableHeaderColumn dataField='badges' dataFormat={badgesFormatter}
                                                   width='20%'>Issues</TableHeaderColumn>
                            </BootstrapTable>
                        </div>
                    </div>
                </div>
                : <PanelLoaderComponent/>
        );
    }
}

export default JiraBoardIssues;
