import React, {Component} from 'react';

import JenkinsService from '../../services/jenkins.service';
import JenkinsBuildsComponent from "../../components/jenkins-builds";
import PanelLoaderComponent from "../../components/panel-loader";

class JenkinsAppiotCoreComponent extends Component {
    constructor() {
        super();
        this.state = {
            builds: []
        };
    }

    getLastBuild() {
        var self = this;
        new JenkinsService().getLastBuilds('jc', ['APPIoT_Autotest_API', 'AppIoT_AutoTest_Frontend', 'AppIoT_Nightly_Deploy_TestNG_V2', 'AppIoT_Server_Full_Unittest'])
            .then(response => {
                self.state.builds = response.data;
                self.setState({
                    builds: self.state.builds
                });
            });
    }

    componentDidMount() {
        var self = this;
        self.getLastBuild();
        const refreshIntervalId = setInterval(() => {
            self.getLastBuild();
        }, 10000);
        this.setState({
            refreshIntervalId,
            builds: this.state.builds
        });

    }

    componentWillUnmount() {
        clearInterval(this.state.refreshIntervalId);
    }

    render() {
        return (this.state.builds.length
            ? <JenkinsBuildsComponent builds={this.state.builds}/>
            : <PanelLoaderComponent/>)
    }
}

export default JenkinsAppiotCoreComponent