import React, {Component} from 'react';
import classNames from 'classnames'

class BadgeComponent extends Component {
    render() {
        var badge = this.props.badge;
        var types = {
            all: "btn-secondary",
            done: "btn-success",
            inprogress: "btn-primary",
            development: "btn-warning",
            qualityassurance:"btn-info"
        };

        var classes = classNames({
            'btn': true,
            'btn-bg': true,
            "btn-secondary": types[badge.type] === "btn-secondary",
            "btn-success": types[badge.type] === "btn-success",
            "btn-primary": types[badge.type] === "btn-primary",
            "btn-info": types[badge.type] === "btn-info",
            "btn-warning": types[badge.type] === "btn-warning"
        });
        return (
            <button type="button" className={classes}>{badge.count}</button>
        );
    }
}

export default BadgeComponent;
