import React, {Component} from 'react';

class ProgressBarComponent extends Component {
    render() {
        return (
            <div style={{width: `${this.props.percent}%`}} className={"progressbar"}>
                <div className={"frame"}>
                    <div className={"fill"}>
                    </div>
                </div>
            </div>
        );
    }
}

export default ProgressBarComponent;
