import React, {Component} from 'react';

import JenkinsService from '../../services/jenkins.service';
import JenkinsBuildsComponent from "../../components/jenkins-builds";
import PanelLoaderComponent from "../../components/panel-loader";

class JenkinsAllBuildsComponent extends Component {
    constructor() {
        super();
        this.state = {
            builds: []
        };
    }

    getLastBuild() {
        var self = this;
        const jlJenkinsJobs = ['bootstrap-server-build-test-merge', 'bssi-build', 'bssi-test', 'lwm2m-gateway-build-test-merge', 'lwm2m-gateway-test-image', 'pvmg-build-test-merge'];
        const jcJenkinsJobs = ['APPIoT_Autotest_API', 'AppIoT_AutoTest_Frontend', 'AppIoT_Nightly_Deploy_TestNG_V2', 'AppIoT_Server_Full_Unittest'];
        Promise.all([new JenkinsService().getLastBuilds('jl', jlJenkinsJobs)])//, new JenkinsService().getLastBuilds('jc', jcJenkinsJobs)
            .then(responses => {
                self.state.builds = responses.reduce((acc, response) => [...acc, ...response.data], []);
                self.setState({
                    builds: self.state.builds
                });
            });
    }

    componentDidMount() {
        var self = this;
        self.getLastBuild();
        const refreshIntervalId = setInterval(() => {
            self.getLastBuild();
        }, 10000);

        this.setState({
            refreshIntervalId,
        });
    }

    componentWillUnmount() {
        clearInterval(this.state.refreshIntervalId);
    }

    render() {
        return (this.state.builds.length > 0
            ? <div className={'jenkins-builds-all'}>
                <JenkinsBuildsComponent builds={this.state.builds}/>
            </div>
            : <PanelLoaderComponent/>)
    }
}

export default JenkinsAllBuildsComponent