import moment from "moment/moment";

class JiraChartIssuesConverter {
    groupIssuesByDateRange(issues, startDate, endDate, dateFiled = 'created') {
        let items = issues.map(item => this.convertItem(item, dateFiled));
        return this.group(items, startDate, endDate);
    }

    getDaysBetweenDates(startDate, endDate, format = 'DD-MMMM') {
        var now = startDate.clone(), dates = [];

        while (now.isSameOrBefore(endDate)) {
            dates.push(now.format(format));
            now.add(1, 'days');
        }
        return dates;
    }

    convertItem(item, dateFiled = 'created') {
        var day = moment.utc(item[dateFiled]).format('DD');
        var month = moment.utc(item[dateFiled]).format('MMMM');
        return {
            ...item,
            day,
            month,
            date: `${day}-${month}`
        }
    }

    group(issues, startDate, endDate) {

        var datesRangeForPeriod = this.getDaysBetweenDates(startDate, endDate);
        var grouped = {};

        datesRangeForPeriod.forEach(date => {
            grouped[`${date}`] = 0;
        });

        issues.forEach(item => {
            grouped[`${item.date}`] += 1;
        });

        return grouped;
    }


}

export default JiraChartIssuesConverter;