import React, {Component} from 'react';

class SlotsComponent extends Component {
    render() {
        var id = this.props.id;
        var lastExecutionTime = this.props.lastExecutionTime;
        return (
            <div>
                <div className="slots">
                    <div className={"build-number slot slot-1"}>
                        <a className={"build-name"}>
                            <span> #{id}</span>
                        </a>
                    </div>
                    <div className={"build-time slot slot-2"}>
                        <div className={"build-time"}>
                                        <span
                                            className={"estimated"}>{lastExecutionTime}</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default SlotsComponent;
