import React, {Component} from 'react';
import BuildComponent from "./build.component";
import AlertComponent from "../alert";

import './styles.css'

class JenkinsBuildsComponent extends Component {
    render() {
        let builds = this.props.builds;

        return (
            <div className={"build-monitor"}>
                <ul id="widgets"
                    className={'columns-2'}>
                    {builds.length
                        ? builds.map(build => <BuildComponent key={build.displayName} data={build}/>)
                        : <AlertComponent message={'Loading failed. No build results loaded.'}/>}
                </ul>
            </div>
        );
    }
}

export default JenkinsBuildsComponent