import React, {Component} from 'react';
import {Line} from 'react-chartjs'
import moment from "moment";
import JiraService from "../../services/jira.service";
import JiraChartIssuesConverter from "./jira-chart-issues.converter";
import './styles.css'
import PanelLoaderComponent from "../../components/panel-loader";

class JiraResolveIssuesComponent extends Component {

    constructor() {
        super();

        this.jiraChartIssuesConverter = new JiraChartIssuesConverter();
        this.currentDate = moment.utc(new Date());
        this.prevMonthDate = this.currentDate.clone().subtract(1, 'months');
        this.state = {
            options: {
                bezierCurve: false,
                responsive: true,
                scaleShowGridLines: true,
                scaleFontSize: 18,
                scaleFontColor: 'white'
            }
        };
    }

    getIssues() {
        // new JiraService().getIssuesByPeriod(this.prevMonthDate, this.currentDate)
        //     .then(response => {
        //         if (response) {
        //             this.state = {
        //                 ...this.state,
        //                 createdCount: response.created.length,
        //                 resolvedCount: response.resolved.length,
        //                 data: this.getChartData(response)
        //             };
        //             this.setState({
        //                 ...this.state
        //             });
        //         }
        //     });
        this.state = {
            ...this.state,
            createdCount: 100,
            resolvedCount: 150,
            data: this.getChartData({})
        };
        this.setState({
            ...this.state
        });
    }

    componentDidMount() {
        this.getIssues();
        var refreshIntervalId = setInterval(() => {
            this.getIssues();
        }, 1000*60*5);

        this.setState({
            refreshIntervalId
        });

    }

    componentWillUnmount() {
        clearInterval(this.state.refreshIntervalId);
    }

    getChartData(data) {
        var created = {"18-June":3,"19-June":6,"20-June":7,"21-June":3,"22-June":1,"23-June":0,"24-June":0,"25-June":9,"26-June":7,"27-June":5,"28-June":20,"29-June":6,"30-June":0,"01-July":0,"02-July":10,"03-July":9,"04-July":3,"05-July":8,"06-July":11,"07-July":0,"08-July":1,"09-July":5,"10-July":14,"11-July":16,"12-July":7,"13-July":7,"14-July":1,"15-July":0,"16-July":6,"17-July":2,"18-July":3};//this.jiraChartIssuesConverter.groupIssuesByDateRange(data.created, this.prevMonthDate, this.currentDate, 'created');
        var resolved = {"18-June":9,"19-June":8,"20-June":7,"21-June":6,"22-June":0,"23-June":0,"24-June":0,"25-June":5,"26-June":6,"27-June":10,"28-June":16,"29-June":11,"30-June":0,"01-July":0,"02-July":2,"03-July":0,"04-July":0,"05-July":3,"06-July":0,"07-July":0,"08-July":0,"09-July":1,"10-July":0,"11-July":9,"12-July":0,"13-July":1,"14-July":0,"15-July":0,"16-July":1,"17-July":0,"18-July":5};//this.jiraChartIssuesConverter.groupIssuesByDateRange(data.resolved, this.prevMonthDate, this.currentDate, 'resolutiondate');
        console.log(JSON.stringify(created));
        console.log(JSON.stringify(resolved));
        return {
            labels: Object.keys(created),
            datasets: [
                {
                    label: "Created",
                    fillColor: 'rgba(255,0,0,0.2)',
                    strokeColor: 'rgba(255,0,0,1)',
                    pointColor: 'rgba(255,0,0,1)',
                    pointStrokeColor: 'red',
                    pointHighlightFill: 'red',
                    pointHighlightStroke: 'rgba(220,220,220,1)',
                    data: Object.values(created)
                },
                {
                    label: "Resolved",
                    fillColor: 'rgba(0,120,0,0.2)',
                    strokeColor: 'rgba(0,120,0,1)',
                    pointColor: 'rgba(0,120,0,1)',
                    pointStrokeColor: '#fff',
                    pointHighlightFill: '#fff',
                    pointHighlightStroke: 'rgba(151,187,205,1)',
                    data: Object.values(resolved)
                }
            ]
        };

    }

    render() {
        return this.state.data
            ? <div className={"chart-body"}>
                <h4 className="title">Created vs Resolved Issues Report</h4>
                <div>
                    <ul className={"legend"}>
                        <li><span className={"box red"}></span> <span
                            className={"label"}>Created: {this.state.createdCount}</span></li>
                        <li><span className={"box green"}></span><span
                            className={"label"}>Resolved: {this.state.resolvedCount}</span></li>
                    </ul>
                </div>
                <Line className={'charts'}
                      data={this.state.data}
                      options={this.state.options}/>
            </div>
            : <PanelLoaderComponent/>
    }
}

export default JiraResolveIssuesComponent