import React, {Component} from 'react';

import JenkinsService from '../../services/jenkins.service';
import JenkinsBuildsComponent from "../../components/jenkins-builds";
import PanelLoaderComponent from "../../components/panel-loader";

class JenkinsAppiotLwm2mComponent extends Component {
    constructor() {
        super();
        this.state = {
            builds: []
        };
    }

    getLastBuild() {
        var self = this;
        new JenkinsService().getLastBuilds('jl', ['bootstrap-server-build-test-merge', 'bssi-build', 'bssi-test', 'lwm2m-gateway-build-test-merge', 'lwm2m-gateway-test-image', 'pvmg-build-test-merge'])
            .then(response => {
                if (response.data) {
                    self.state.builds = response.data.map(item => item);
                    self.setState({
                        builds: self.state.builds
                    });
                }
            });
    }

    componentDidMount() {
        var self = this;
        self.getLastBuild();
        const refreshIntervalId = setInterval(() => {
            self.getLastBuild(refreshIntervalId);
        }, 10000);
        this.setState({
            refreshIntervalId,
            builds: this.state.builds
        });

    }

    componentWillUnmount() {
        clearInterval(this.state.refreshIntervalId);
    }

    render() {
        return (this.state.builds.length
            ? <JenkinsBuildsComponent builds={this.state.builds}/>
            : <PanelLoaderComponent/>)
    }
}

export default JenkinsAppiotLwm2mComponent